import React from "react";
import '../../post.css'

export function HomePage() {

    const readMore = () => {
        var dots = document.getElementById("dots");
        var moreText = document.getElementById("more");
        var btnText = document.getElementById("myBtn");
      
        if (dots.style.display === "none") {
          dots.style.display = "inline";
          btnText.innerHTML = "Czytaj dalej"; 
          moreText.style.display = "none";
        } else {
          dots.style.display = "none";
          btnText.innerHTML = "Czytaj mniej"; 
          moreText.style.display = "inline";
        }
    };
      

  return (
    <div>
      <div className="App">
        <div className="Content">
            <div className="post">
              <img
                src={require("../../assets/afc-lfc.webp")}
                alt="post"
              />
              <h1> Remis jak zwycistwo: Liverpool 0-0 Arsenal</h1>
              <p>
                Kanonierzy zdoali wczoraj bezbramkowo zremisowa na Anfield
                Road i tym samym podtrzymali nadzieje na awans do finau Carabao
                Cup. Sam mecz dla postronnego kibica mg wydawa si nieco
                nudny, jednak fani Arsenalu powinni by zadowoleni z kocowego
                rezultatu, biorc pod uwag, e wiksz cz meczu ich pupile
                grali w osabieniu.
                <span id="dots">...</span>
                <span id="more">
                  Kluczowa dla przebiegu spotkania bya 24. minuta, gdy Granit
                  Xhaka po raz kolejny w tym sezonie bezmylnie faulowa, w tym
                  przypadku wychodzcego na czyst pozycj Diogo Jot, a arbiter
                  Michael Olivier bez wahania wyrzuci go za to zagranie z
                  boiska. Mimo optycznej przewagi gospodarzy, Arsenal prbowa
                  wyprowadza ataki i nie ogranicza si jedynie do bronienia
                  dostpu do wasnej bramki. Cho w statystykach ujrzymy spor
                  przewag The Reds w posiadaniu piki (77-23%), to obie druyny
                  zdoay odda zaledwie po jednym celnym strzale, co najlepiej
                  obrazuje przebieg spotkania. Fanom Arsenalu pozostaje mie
                  nadziej, e czwartkowy pojedynek nie odcinie zbyt duego
                  pitna na przygotowaniach zespou do niedzielnych derbw z
                  Tottenhamem. Wiadomo ju bowiem, e w rodku pola nie bdzie
                  mg zagra Xhaka, a Mikel Arteta wiadom trudnej sytuacji
                  kadrowej, wezwa ju pono z wypoyczenia Miguela Azeeza.
                  Wczorajszego wieczoru muraw kontuzjowany opuszcza take
                  Bukayo Saka, cho nie wiadomo jeszcze, czy ten uraz wykluczy
                  go z gry ze Spurs. Cho dla Kanonierw nie by to wymarzony
                  wieczr, to ten remis na Anfield naley przyj z pokor,
                  patrzc na histori spotka obu ekip w miecie Beatlesw.
                </span>
              </p>
              <button onClick={readMore} id="myBtn">
                Czytaj dalej
              </button>
            </div>
        </div>
      </div>
    </div>
  );
}
